.activate-spin {
  animation-name: rotatespin;
  animation-duration: 9s;
  transition: transform 9s ease-out;
  -webkit-transition: transform 9s ease-out;
  -webkit-animation-name: rotatespin;
  -webkit-animation-duration: 9s;
}

@-webkit-keyframes rotatespin {
  0% {
    left: 50%;
    transform: translateX(-50%) rotateZ(0deg);
    -webkit-transform: translateX(-50%) rotateZ(0deg);
  }
  100% {
    left: 50%;
    transform: translateX(-50%) rotateZ(1080deg);
    -webkit-transform: translateX(-50%) rotateZ(1080deg);
  }
}
@keyframes rotatespin {
  0% {
    left: 50%;
    transform: translateX(-50%) rotateZ(0deg);
    -webkit-transform: translateX(-50%) rotateZ(0deg);
  }
  100% {
    left: 50%;
    transform: translateX(-50%) rotateZ(1080deg);
    -webkit-transform: translateX(-50%) rotateZ(1080deg);
  }
}
.plate:after,
.plate:before {
  content: "";
  display: block;
  position: absolute;
  border-radius: 50%;
}
.plate:after {
  top: -6px;
  right: -6px;
  bottom: -6px;
  left: -6px;
  border: 6px solid #000000;
  box-shadow: inset 0px 0px 0px 3px #e3e3e3, 0px 0px 0px 10px #000000;
}
.plate:before {
  background: rgba(0, 0, 0, 0.65);
  border: 1px solid #e3e3e3;
  box-shadow: inset 0px 0px 0px 2px #e3e3e3;
  top: 12%;
  left: 12%;
  right: 12%;
  bottom: 12%;
  z-index: 1;
}
.number {
  width: 32px;
  height: 175px;
  display: inline-block;
  text-align: center;
  position: absolute;
  top: 0;
  left: calc(50% - (32px / 2));
  transform-origin: 50% 100%;
  background-color: transparent;
  border-left: 16px solid transparent;
  border-right: 16px solid transparent;
  border-top: 175px solid #04000f;
  box-sizing: border-box;
}
.number:nth-child(odd) {
  border-top-color: #fe2247;
}
.number:nth-child(38),
.number-new {
  border-top-color: #419e3f !important;
}
.pit {
  color: #fff;
  padding-top: 12px;
  width: 32px;
  display: inline-block;
  font-size: 13px;
  /* transform: scale(1, 1.8); */
  position: absolute;
  top: -175px;
  left: -16px;
  font-weight: 500;
}
.inner {
  display: block;
  height: 350px;
  width: 350px;
  position: relative;
  margin: 0;
}
.inner:after,
.inner:before {
  content: "";
  display: block;
  position: absolute;
  border-radius: 50%;
}
.inner:after {
  z-index: 3;
  top: 24%;
  right: 24%;
  bottom: 24%;
  left: 24%;
  background-color: #000000;
  border: 3px solid #e3e3e3;
}
.inner:before {
  top: 20%;
  bottom: 20%;
  left: 20%;
  right: 20%;
  content: "";
  color: #fff;
  font-size: 60px;
  z-index: 5;
  border-radius: 0;
}
