.btn .btn-label {
  padding: 12px;
  white-space: nowrap;
}
.btn.btn-reset {
  background: #21c9a6;
  border: 1px solid #21c9a6;
}
.btn.disabled {
  opacity: 0.2;
  transition: opacity 0.24s linear;
  cursor: not-allowed;
}

@-webkit-keyframes rotate {
  0% {
    transform: rotateZ(0deg);
  }
  100% {
    transform: rotateZ(360deg);
  }
}
@keyframes rotate {
  0% {
    transform: rotateZ(0deg);
  }
  100% {
    transform: rotateZ(360deg);
  }
}
.data {
  display: block;
  position: absolute;
  top: 30%;
  right: 30%;
  bottom: 30%;
  left: 30%;
  border-radius: 50%;
  /* animation: rotate 24s reverse linear infinite; */
  perspective: 2000px;
  z-index: 9;
}
.data .data-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.72s;
  transform-style: preserve-3d;
}
.data.reveal .data-inner {
  transform: rotateY(180deg);
}
.data .mask,
.data .result {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  position: absolute;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  border-radius: 50%;
  overflow: hidden;
  display: inline-block;
}
.data .mask {
  color: #fff;
  font-size: 20px;
  margin: auto;
  line-height: 1.4;
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: center;
}
.data .result {
  background-color: #21c9a6;
  transform: rotateY(180deg);
  align-items: center;
  color: #fff;
}
.data .result-number {
  font-size: 72px;
  font-weight: 500;
  line-height: 1.2;
  margin-top: 10px;
}
.data .result-color {
  text-transform: uppercase;
  font-size: 21px;
  line-height: 1;
}
.previous-results {
  max-width: 150px;
  position: fixed;
  text-transform: capitalize;
  right: 10px;
  top: 50px;
  width: 100%;
}
.previous-list {
  perspective: 2000;
  margin: 0;
}
.previous-result {
  display: flex;
  margin: 1px;
  padding: 6px 12px;
  color: #fff;
}
.previous-result:first-child {
  -webkit-animation: flipin 0.48s ease-out;
  animation: flipin 0.48s ease-out;
}
.previous-result .previous-number {
  flex: 1;
}
.color-green {
  background-color: #21c9a6;
}
.color-black {
  background-color: black;
}
.color-red {
  background-color: #eb2958;
}
.visuallyhidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}
@-webkit-keyframes flipin {
  0% {
    transform: rotateX(90deg);
  }
  100% {
    transform: rotateX(0deg);
  }
}
@keyframes flipin {
  0% {
    transform: rotateX(90deg);
  }
  100% {
    transform: rotateX(0deg);
  }
}

.data-inner span {
  color: #fff;
  font-size: 25px;
}

/***************** Bet Table *******************/

.roulette-bet-table {
  width: 720px;
  margin: 0px auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  padding-top: 25px;
}

.roulette-bet-table table.bet-table {
  font-size: 24px;
}
.roulette-bet-table table {
  text-align: center;
  border-collapse: separate;
  border-spacing: 0;
  width: 100%;
  cursor: pointer;
}

.roulette-bet-table table tr {
  font-size: 16px;
  line-height: 0px;
}

.roulette-bet-table table.bet-table tr:first-child td:first-child {
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
  border-left: 2px solid #f8f5ff;
}
.roulette-bet-table table.bet-table tr:first-child td:nth-child(2),
.roulette-bet-table table.bet-table tr:first-child td:nth-child(3) {
  border-top: 2px solid white;
}
.roulette-bet-table table.bet-table tr:first-child td:nth-child(3) {
  border-right: 2px solid white;
}
.roulette-bet-table table.bet-table tr:first-child td {
  border-top: 0px solid #f8f5ff;
  border-bottom: 2px solid white;
}
.roulette-bet-table table.bet-table td {
  padding: 0;
  font-size: 16px;
  font-weight: 600;
  /* border-right: 1px solid #f8f5ff;
  border-bottom: 1px solid #f8f5ff; */
  height: 50px;
  width: 50px;
  color: #fff;
  vertical-align: middle;
}
.red {
  background-color: #fe2247 !important;
}

.black {
  background-color: black !important;
}

.green {
  background-color: #419e3f !important;
}

.roulette-bet-table table.bet-table tr:first-child td:last-child {
  border-top-right-radius: 4px;
}

.roulette-bet-table table.bet-table tr td:first-child,
.roulette-bet-table table.bet-table tr td:last-child {
  width: 60px;
}

.roulette-bet-table table.bet-table td:last-child {
  font-size: 16px;
  background-color: #121b24;
}

.roulette-bet-table .grid-bet-table > div:first-child {
  border-radius: unset;
  border-top-left-radius: 4px;
}

