.winning-animation {
  position: fixed;
  z-index: 999999;
  transform: translate(-50%, -50%);
  left: 50%;
  top: 50%;
  height: 100%;
  width: 100%;
}
.winning-animation:before {
  content: "";
  position: absolute;
  background: #000000cc;
  height: 100%;
  width: 100%;
  z-index: 999999;
}
.winning-animation .winning-amount {
  color: white;
  z-index: 9999999999;
  position: absolute;
  transform: translate(-50%, -50%) scale(0);
  top: 50%;
  left: 50%;
  font-size: 40px;
  color: #f7f7f7;
  padding: 41px 20px;
  max-width: 600px;
  width: 100%;
  text-align: center;
  background-image: linear-gradient(to right, #eb295800, #eb2958, #eb295800);
  animation: neon1 0.5s ease-in-out;
  animation-fill-mode: forwards;
}
@keyframes neon1 {
  from {
    transform: translate(-50%, -50%) scale(0);
  }
  to {
    transform: translate(-50%, -50%) scale(1);
  }
}
.winning-animation .winning-amount h2 {
  color: white;
  font-size: 48px;
  text-align: center;
  font-family: 'Righteous', cursive !important;
  margin-bottom: 10px;
}

.winning-amount p {
  font-size: 16px;
  margin: 0;
  color: white;
  font-weight: 400;
}
.close-img img {
  position: absolute;
  z-index: 9999999;
  height: 22px;
  right: 16px;
  top: 15px;
  cursor: pointer;
}
.stat-meter-red,
.stat-meter-green,
.stat-meter-dark,
.stat-meter-odd {
  min-width: fit-content;
}
.winning-animation-win .winning-amount {
  background-image: linear-gradient(to right, #337a1700, #337a17, #337a1700);
}

.winns img {
  width: 40px;
  border-radius: 100%;
  margin-right: 10px;
  height: 40px;
}
.flex-win {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  max-width: 60%;
  margin: 5px auto;
  font-size: 15px;
}

.flex-win img {
  height: 18px;
  width: 18px;
  margin-left: 5px;
}
.flex-win span {
  width: 40%;
  display: flex;
  align-items: center;
}

/***** slot result *********/
.left-slot-result {
  width: 40%;
}

.right-slot-result {
  width: 60%;
}

.right-slot-result img {
  height: 50px !important;
  width: 100% !important;
  object-fit: contain;
}

.slot-result-section {
  display: flex;
  margin: 20px 0px;
}

.slot-images {
  display: flex;
  margin-bottom: 10px;
}

.slot-numbers {
  min-height: 50px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 10px;
}
.winner-section.last-result{
  margin-top: 10px !important;
}
.winner-section.last-result p {
  color: white;
  font-size: 14px;
  font-weight: 400;
}
.winner-section.last-result .result-listing {
  display: flex;
  /* border-top: 1px dashed #434343; */
  padding: 3px 0px;
  margin: 5px 0px;
  width: 40%;
  align-items: center;
}

.winner-section.last-result .result-listing p {
  width: 50%;
  margin: 0;
  font-size: 12px;
}

.winner-section.last-result .result-listing span {
  color: #b9b9b9;
  width: 50%;
  text-align: right;
  font-size: 12px;
}
.result-listiing-section{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}